<template>
  <tas-base-crud :config="config" hide-create hide-update hide-delete hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="getSlaveList(data.rowData)" class="btn btn-info btn-sm">Lihat Daftar Komponen</a>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-mapping-salary-accounts',
  data () {
    return {
      config: {
        title: 'Setup Template Gaji Tenaga Kerja',
        model_api: null,
        getter: 'custom/mapping-template-salary-employees/list-employees',
        setter: 'custom/mapping-template-salary-employees/list-employees',
        custom_api: {
          list: 'custom/mapping-template-salary-employees/list-employees',
          read: 'custom/mapping-template-salary-employees/show-employees'
        },
        pk_field: 'fullname',
        permission: {
          create: 'create-mapping-salary-accounts',
          read: 'view-mapping-salary-accounts',
          update: 'update-mapping-salary-accounts',
          delete: 'delete-mapping-salary-accounts'
        },
        slave: [

        ],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'fullname', label: 'Nama Lengkap', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          { id: 'nik', label: 'NIK', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          { id: 'nip', label: 'NIP', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          {
            id: 'employment_level_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              detail: { view_data: 'rel_job_position_id' }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id' },
              detail: { view_data: 'rel_status_employment_id' }
            }
          }
        ]
      }
    }
  },
  methods: {
    getSlaveList (e) {
      let slaveList = []
      let def = {
        name: 'Mapping Akun Penggajian',
        module: 'payroll/mapping-template-salary-employees',
        as_param: 'employee_id',
        key_field: 'id',
        overwrite: {
          filter_api: { 'employee_id': e.id },
          custom_api: {
            read: 'custom/mapping-template-salary-employees/show?employee_id=' + e.id
          },
          fields: [
            { rule: '1.methods.list', value: false },
            { rule: '1.methods.filter', value: false },
            { rule: '1.methods.create', value: false },
            { rule: '1.methods.update', value: false },
            { rule: '1.methods.detail', value: false }
          ]
        }
      }
      slaveList.push(def)
      this.$set(this.config, 'slave', slaveList)
      this.$nextTick().then(f => {
        this.$children[0].getEvent('detail', e)
      })
    }
  }
}
</script>



